<template>
    <div id="takeout-order">
        <public-header title="订单设置"></public-header>
        <div class="content">
            <van-cell-group>
                <van-cell>
                    <div slot="title">配送员接单后是否通知商户</div>
                    <iswitch :value="order.deliveryer_collect_notify_clerk" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="order.deliveryer_collect_notify_clerk" ></iswitch>
                </van-cell>
                <van-cell>
                    <div slot="title">未支付订单在待接单列表展示</div>
                    <iswitch :value="order.show_no_pay" condition-open="0" condition-close="1" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="order.show_no_pay"></iswitch>
                </van-cell>
            </van-cell-group>
            <div class="padding-15">
                <van-button size="normal" class="bg-primary font-16" block @click="onSubmit">提交</van-button>
            </div>
        </div>
        <iloading v-if="showPreLoading"></iloading>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				order:{
					deliveryer_collect_notify_clerk: '1',
					show_no_pay: '1',
				}
			}
		},
		components: {
			publicHeader,
			iswitch
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: this,
					autoAssign: true,
					variable: 'order',
					url: 'plateform/agent/config/order'
				});
			},

			jsToggleSwitch(res){
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
            },

			onSubmit(){
				var that = this;
				setData({
					vue: this,
					url: 'plateform/agent/config/order',
					data: that.order
				});
            }
		},
        mounted(){
			this.onLoad();
        }
	}
</script>

<style>

</style>


